enum Grid {
  Blank = ' ',
  Peg = 'o',
  Wall = 'x',
}

class PegSolitaire {
  public static readonly SIZE: number = 7;
  public board: Grid[][];
  public initialBoard: Grid[][];

  constructor(pegs: [number, number][]) {

    this.board = Array(PegSolitaire.SIZE).fill(null).map((_, i) => {
      return Array(PegSolitaire.SIZE).fill(null).map((_, j) => {
        if ((i > 1 && i < 5) || (j > 1 && j < 5)) return Grid.Blank;
        else return Grid.Wall;
      });
    });

    pegs.forEach(([i, j]) => this.board[i][j] = Grid.Peg);

    this.initialBoard = PegSolitaire.copyBoard(this.board);
  }

  reset() {
    this.board = PegSolitaire.copyBoard(this.initialBoard);
  }

  count(): number {
    return this.board.flat().reduce((count, grid) => {
      return grid === Grid.Peg ? count + 1 : count;
    }, 0);
  }

  onBoard(i: number, j: number): boolean {
    return i >= 0 && i < PegSolitaire.SIZE && j >= 0 && j < PegSolitaire.SIZE;
  }

  isPeg(i: number, j: number): boolean {
    return this.board[i][j] == Grid.Peg;
  }

  isBlank(i: number, j: number): boolean {
    return this.board[i][j] == Grid.Blank;
  }

  getGrid(i: number, j: number): Grid {
    return this.board[i][j]
  }

  movable(i: number, j: number): boolean {
    if (!this.onBoard(i, j) || !this.isPeg(i, j)) return false;
    return this.getLegalDestinations(i, j).length != 0;
  }

  getLegalDestinations(i: number, j: number): [number, number][] {
    const moves = [[0, -1], [0, 1], [-1, 0], [1, 0]].reduce((moves: [number, number][], [di, dj]) => {
      const midI = i + di;
      const midJ = j + dj;
      const toI = midI + di;
      const toJ = midJ + dj;
      if (this.onBoard(toI, toJ) && this.isPeg(midI, midJ) && this.isBlank(toI, toJ)) {
        moves.push([toI, toJ]);
      }
      return moves;
    }, [])
    // console.log(moves);
    return moves;
  }

  move(i: number, j: number, toI: number, toJ: number) {
    const midI = (i + toI) / 2;
    const midJ = (j + toJ) / 2;
    this.board[i][j] = Grid.Blank;
    this.board[midI][midJ] = Grid.Blank;
    this.board[toI][toJ] = Grid.Peg;
  }

  static copyBoard(board: Grid[][]): Grid[][] {
    return board.map(row => [...row]);
  }
}

export default PegSolitaire;
export { Grid };
