import { View } from 'react-native';
import GridView from "./GridView";
import PegSolitaire from "./PegSolitaire";


interface BoardViewProps {
  game: PegSolitaire;
  boardLength: number;
  cell: [number, number] | null;
  setCount: (count: number) => void;
  setCell: (cell: [number, number] | null) => void;
}

function BoardView({ game, boardLength, cell, setCount, setCell }: BoardViewProps) {
  const rowViews = game.board.map((row, irow) => {
    const rowView = row.map((_, icol) => {
      return <GridView key={`grid${irow}${icol}`} irow={irow} icol={icol} cell={cell} setCell={setCell} setCount={setCount} game={game} />
    });
    return <View key={`row${irow}`} style={{ flex: 1, width: boardLength, flexDirection: "row" }} >{rowView}</View>
  });
  return <View style={{ height: boardLength }} >{rowViews}</View>;
}


export default BoardView;