import { useEffect, useRef, useState } from 'react';
import { Dimensions, Image, View } from 'react-native';
import BoardView from './BoardView';
import MyButton from './MyButton';
import PegSolitaire from './PegSolitaire';
import { BG_COLOR } from './colors';
import { loadGameWithValidation } from './game-loader';
import { sharePuzzle } from './twitter';

const WILL_VERIFY_SIGNATURE = false;
const STAMP_IMAGE = require('./images/stamp.png');


export default function App() {
  const [articleUrl, setArticleUrl] = useState<string>();
  const [puzzle, setPuzzle] = useState<string>();
  const [game, setGame] = useState<PegSolitaire>();
  const [count, setCount] = useState<number>();
  const [cell, setCell] = useState<[number, number] | null>(null);
  const [boardLength, setBoardLength] = useState<number>(0);
  const [ready, setReady] = useState<boolean>(false);
  const buttonViewRef = useRef<View>(null);

  useEffect(() => {
    const buttonView = buttonViewRef.current;
    if (!ready || !buttonView) return;

    const updateLayout = () => {
      buttonView.measure((x, y, width, height, pageX, pageY) => {
        const window = Dimensions.get('window');
        const length = Math.min(window.height - height - 15, window.width - 17);
        setBoardLength(length);
      });
    }

    updateLayout();
    const subscription = Dimensions.addEventListener('change', updateLayout);

    return () => subscription.remove();
  }, [ready]);

  useEffect(() => {
    const initializeGame = async () => {
      const { articleUrl: _articleUrl, puzzle: _puzzle, game: _game } = await loadGameWithValidation(WILL_VERIFY_SIGNATURE);
      setArticleUrl(_articleUrl);
      setPuzzle(_puzzle);
      setGame(_game);
      setCount(_game.count());
    }

    initializeGame()
  }, []);

  const handleRetryPress = () => {
    if (!game) return;
    game.reset();
    setCount(game.count());
    setCell(null);
  }

  const handleSharePress = (url: string | undefined) => {
    if (url) sharePuzzle(url);
    else window.alert("このパズルに対応するTwitter記事のURLが登録されていません。");
  }

  if (puzzle && game && count) {
    if (!ready) setReady(true);
    return <View style={{
      flex: 1,
      backgroundColor: BG_COLOR,
      paddingTop: 10,
      paddingLeft: 10,
    }}>
      {/* <StatusBar /> */}
      <View style={{ position: 'relative' }}>
        <BoardView game={game} boardLength={boardLength} cell={cell} setCell={setCell} setCount={setCount} />
        {count <= 1 && <SolvedImage boardLength={boardLength} />}
      </View>
      <View ref={buttonViewRef} style={{ width: boardLength, paddingTop: 2 }}>
        <MyButton title="はじめからやりなおす" onPress={() => handleRetryPress()} />
        <MyButton title="ツイッターでシェアする" onPress={() => handleSharePress(articleUrl)} />
        {/* <MyButton title="正解者リストをみる" onPress={() => checkCorrectAnswerers()} /> */}
        {/* <MyButton title="正解者リストに登録する" onPress={() => registerAnswerer(puzzle)} /> */}
      </View>
    </View>
  } else {
    return <View style={{ flex: 1 }} />
  }
}

interface SolvedImageProps {
  boardLength: number;
}

function SolvedImage({ boardLength }: SolvedImageProps) {
  const imageLength = boardLength / 1.618;
  const imageOffset = (boardLength - imageLength) / 2;
  return <Image style={{
    position: 'absolute', resizeMode: "contain",
    width: imageLength, height: imageLength,
    top: imageOffset, left: imageOffset,
  }} source={STAMP_IMAGE} />
}

