import * as Linking from 'expo-linking';
import PegSolitaire from './PegSolitaire';

const SERVER_URL = "http://localhost:3000";

function getArticleUrl(puzzle: string): string | undefined {
  const ARTICLE_URLS_JSON = require("./data/article_urls.json");
  return ARTICLE_URLS_JSON[puzzle];
}

function verifySignature(signature: string, puzzle: string): Promise<boolean> {
  return fetch(SERVER_URL + "/verify-signature", {
    method: "POST",
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      puzzle: puzzle,
      signature: signature,
    })
  })
    .then(response => response.json())
    .then(data => {
      if (data.message === "ok") {
        return true;
      } else {
        return false;
      }
    })
    .catch(error => {
      throw new Error(error);
    });
}

function decodePuzzle(puzzle: string): [number, number][] {
  const charToIndices = (c: string): [number, number] => {
    const baseLowerCase = 'a'.charCodeAt(0);
    const baseUpperCase = 'A'.charCodeAt(0);

    let code = c.charCodeAt(0);

    if (c >= 'a' && c <= 'z') code = code - baseLowerCase;
    else if (c >= 'A' && c <= 'Z') code = code - baseUpperCase + 26;
    else throw new Error('Only ASCII letters are allowed.');

    return [Math.floor(code / PegSolitaire.SIZE), code % PegSolitaire.SIZE];
  }

  return puzzle.split('').map(c => charToIndices(c));
}

async function loadGame(willVerifySignature: boolean) {
  const url = await Linking.getInitialURL();
  if (!url) throw new Error("URL not found.");

  const params = Linking.parse(url).queryParams;
  if (!params) throw new Error("Query parameters not found.");

  const puzzle = params.puzzle as string;
  if (!puzzle) throw new Error("Query 'puzzle' not found.");

  const articleUrl = getArticleUrl(puzzle);

  if (willVerifySignature) {
    const signature = params.signature as string;
    if (!signature) throw new Error("Query 'signature' not found.");

    if (!await verifySignature(signature, puzzle)) {
      throw new Error("Signature verification failed.");
    };
  }

  const game = new PegSolitaire(decodePuzzle(puzzle));

  return { articleUrl, puzzle, game }
}

export { loadGame as loadGameWithValidation };

