import React from 'react';
import { Pressable, StyleSheet, View } from "react-native";
import PegSolitaire, { Grid } from "./PegSolitaire";
import { DARK_COLOR, HIGHLIGHT_COLOR, LIGHT_COLOR } from './colors';


interface GridViewProps {
  irow: number;
  icol: number;
  cell: [number, number] | null;
  setCell: (cell: [number, number] | null) => void;
  setCount: (count: number) => void;
  game: PegSolitaire;
}

function GridView({ irow, icol, cell, setCell, setCount, game }: GridViewProps) {
  const grid = game.getGrid(irow, icol);
  let element: React.JSX.Element;

  if (cell && cell[0] === irow && cell[1] === icol) {
    element = <PegView color={HIGHLIGHT_COLOR} />;
  } else if (grid === Grid.Peg) {
    element = <PegView color={DARK_COLOR} />;
  } else if (grid === Grid.Wall) {
    element = <View style={{ width: "100%", height: "100%" }} />;
  } else {
    element = <View style={styles.grid} />;
  }

  const handlePress = () => {
    if (game.movable(irow, icol)) { setCell([irow, icol]); return; }
    else if (!cell) return;
    else setCell(null);

    game.getLegalDestinations(cell[0], cell[1]).forEach(dest => {
      if (dest[0] === irow && dest[1] === icol) {
        game.move(cell[0], cell[1], dest[0], dest[1]);
        setCount(game.count());
        return;
      }
    });
  }

  return <Pressable onPress={handlePress}
    style={{
      flex: 1,
      marginRight: 3,
      marginBottom: 3,
      justifyContent: "center",
      alignItems: "center",
    }}>
    {element}
  </Pressable>
}

interface PegViewProps {
  color: string;
}

function PegView({ color }: PegViewProps) {
  return <View style={styles.grid}>
    <View style={{
      width: "70%",
      height: "70%",
      borderRadius: Number.MAX_SAFE_INTEGER,
      backgroundColor: color,
    }} />
  </View>
}

const styles = StyleSheet.create({
  grid: {
    width: "100%",
    height: "100%",
    backgroundColor: LIGHT_COLOR,
    justifyContent: "center",
    alignItems: "center",
  }
});

export default GridView;